@keyframes btn-click-animation{
    0%{
        background: radial-gradient(circle, rgba(255,255,255,0.8) 0%, rgba(255,255,255,0) 100%);
    }
    30%{
        background: radial-gradient(circle, rgba(255,255,255,0.8) 0%,rgba(255,255,255,0.8) 30%, rgba(255,255,255,0) 100%);
    }
    100%{
        background: radial-gradient(circle , rgba(255,255,255,1) 0%, rgba(255,255,255,1) 100%);
    }
}
  
ul,
li {
  list-style: none;
  margin: 0;
  padding: 0;
}


.list-item-blankCell {
  z-index: 0;
  width: 100%;
  height: 100%;
  
  border-top: rgb(0, 0, 0, 0.2) 0.05rem solid;
  border-bottom: rgb(0, 0, 0, 0.2) 0.05rem solid;
  border-right: rgb(0, 0, 0, 0.1) 0.05rem solid;
  border-left: rgb(0, 0, 0, 0.1) 0.05rem solid;
  color: rgba(255, 255, 255, 0.95);
  padding: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.list-item-blankCell:hover{
    background-color: red;
}
.li-fillingCell {
    color:rgba(255,255,255,0.9);
    width:100%;
    border:rgb(255,255,255, 0.7) 2px solid;
    padding:0.5rem;
  box-sizing:border-box;
  z-index: 1;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  cursor: pointer;
  font-family: "Kosugi Maru", sans-serif;
  font-weight: bold;
  flex-grow: column wrap;
  word-break: break-all;
  overflow: hidden;
}

.box-scheduleHeader {
    z-index:2;
  width: 100%;
  height: 5rem;
  position: sticky;
  top: 0;
  background-color: #000a12;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.item-scheduleHeader {
  color: rgba(255, 255, 255, 0.95);
}
.box-schedules {
  margin: 0;
  padding: 0;
  width: 100vw;
  height: 90vh;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  overflow-y: scroll;
  background-color: #fffde7;
}
.box-scheduleRow {
    width:9%;
  flex-grow: 3;
  border-right: rgb(0,0,0, 0.2) 0.05rem solid;
  border-left: rgb(0,0,0, 0.2) 0.05rem solid;
}

.li-blankCell {
  width: 100%;
  height: 5rem;
  background-color: #fffde7;
  box-sizing: border-box;
  padding: 0;
  
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}





.item-fillingCell {
    background-color: #00b8d4;
    transition: background-color 150ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
  width: 99%;
  border-top: rgb(255,255,255, 0.6) 0.05rem solid;
  border-bottom: rgb(255,255,255, 0.6) 0.05rem solid;
 
}

.box-clockLine {
  width: 100%;
  height: 1px;
  background: #dd2c00;
  position: relative;
}
.cell-under {
  z-index: 0;
  width: 100%;
  pointer-events: none;
}

.box-scheduleTimeRow {
    width:5%;
    flex-grow: 1;
  height: auto;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}
.item-scheduleTimeRow {
    box-sizing: border-box;
  width: 100%;
  height: 5rem;
  background-color: #263238;
  color: rgba(255, 255, 255, 0.9);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}
.box-odd {
  border-bottom: 0.1rem solid rgba(255, 255, 255, 0.8);
}
.box-even {
    border-bottom: 0.1rem dotted rgba(255, 255, 255, 0.4);
}
.box-odd-black {
    border-bottom: 0.1rem solid rgba(0,0,0, 0.4);
    border-right: rgb(0, 0, 0, 0.3) 1% solid;
  border-left: rgb(0, 0, 0, 0.3) 1% solid;
}
  .box-even-black {
      border-bottom: 0.1rem dotted rgba(0,0,0, 0.2);
  }

  